.el-carousel {
  width: 90%;
  margin: 0 auto;
  margin-top: 30px;
  .economy {
    width: 100%;
    height: 97%;
    display: flex;
    margin: 0 auto;
    .item {
      width: 50%;
      //margin: 0 auto;
      height: 100%;
      padding-left: 70px;
      display: flex;
      justify-content: space-between;
      justify-items: center;
      align-items: center;
      img {
        width: 150px;
        height: 150px;
      }
      .content {
        text-align: right;
        width: auto;
        height: 180px;
        font-size: 50px;
        margin-right: 70px;
        .num {
          padding-bottom: 20px;
        }
        span {
          color: #3cbdc3;
          font-size: 70px;
          font-weight: 900;
        }
      }
    }
  }
}

.right {
  position: absolute;
  width: 1700px;
  height: 2066px;
  right: 0;
  top: 264px;
  .right-layout1 {
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
  }
  .video-layout {
    height: 400px;
    padding-top: 60px;
    position: relative;
    .video-banner {
      position: absolute;
      top: 30px;
      left: 0;
      width: 100%;
      height: 100%;
      .list {
        width: 90%;
        margin-left: 5% !important;
        display: flex;
        // margin: 10px;
        justify-content: space-between;
        .item {
          width: 30%;
          height: 450px;
          background: #fff;
          overflow: hidden;
        }
      }
    }
  }
  .right-back {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;

    background: rgba(2, 15, 43, 0.7);
  }
  .button {
    position: absolute;
    top: -100px;
    right: 0;
    z-index: 100;
    width: 500px;
    height: 100px;
    font-size: 60px;
    color: #fff;
    line-height: 100px;
    cursor: pointer;
    &:hover {
      color: #4f9efd;
    }
    background: rgba(0, 0, 0, 0.068);
  }
  .city {
    padding-top: 60px;
    height: 1200px;
    position: relative;
    .city-layout {
      position: absolute;
      top: 0;
      left: 0;
      right: 0;
      bottom: 0;
      color: #fff;
      .sub-title {
        width: 90%;
        margin-left: 70px;
        margin-top: 60px;
        color: #fff;
        font-size: 50px;
        padding: 10px 0;
      }
      .share {
        width: 90%;
        margin-left: 70px;
        font-size: 48px;
        display: flex;
        margin-top: 40px;
        opacity: 0.7;
        .item {
          width: 30%;
          display: flex;
          justify-content: space-between;
          align-items: center;
          .unit {
            span {
              color: #416cb4;
              font-size: 60px;

              font-weight: 900;
              margin-right: 20px;
            }
          }
        }
        .item:not(:first-child) {
          margin-left: 5%;
        }
      }
      .list {
        padding-top: 63px;
        width: 90%;
        margin-left: 70px;
        display: flex;
        justify-content: space-between;

        .item {
          width: 513px;
          height: 136px;
          border: 1px solid #bbb;
          border-radius: 10px;
          position: relative;
          color: #fff;
          font-size: 38px;
          img {
            position: absolute;
            top: -63.5px;
            left: 40px;
          }
          .name {
            position: absolute;
            bottom: 20px;
            left: 40px;
          }
          .num {
            position: absolute;
            bottom: 20px;
            right: 20px;
            span {
              font-size: 48px;
            }
          }
        }
        .item:not(:first-child) {
          margin-left: 20px;
        }
      }
    }
  }
}
.left {
  width: 1770px;
  height: 2066px;
  top: 300px;
  position: absolute;
  left: 0px;
  .red-tag {
    position: relative;
    height: 341px;
    font-size: 14px;
    .ling {
      height: 400px;
      width: 50%;
      padding-top: 67px;
      .item {
        display: flex;
        width: 90%;
        height: 20%;
        margin: 0 auto;

        align-items: center;
        justify-content: center;
        font-size: 40px;
        color: #fff;
        .linner {
          margin-left: 40px;
          margin-right: 40px;
          flex: 1;
          height: 20px;
          border-radius: 20px;
          background: linear-gradient(
            to left,
            rgba(80, 155, 246, 0.1) 1%,
            rgba(80, 155, 246, 0.1) 30%,
            rgba(80, 155, 246, 0.8) 40%
          );
        }
      }
    }
    .res-layout {
      display: flex;
      height: 100%;
      flex-wrap: wrap;

      .item {
        width: 48%;
        height: 46%;
        margin: 0.7%;
        opacity: 0.8;
        position: relative;
        .title {
          color: #4f9efd;
          font-size: 50px;
          top: 30px;
          left: 30px;
          position: absolute;
        }
        .content {
          position: absolute;
          top: 110px;
          left: 30px;
          height: 100px;
          width: 80%;
          .content-layout {
            display: flex;
            flex-direction: row;
            align-items: center;
            width: 100%;
            height: 100%;
            // justify-content: space-between;
            //line-height: 100px;
            img {
              width: 50px;
              height: 50px;
            }
          }
        }
        .back {
          position: absolute;
          width: 100%;
          height: 100%;
          top: 0;
          left: 0;
        }
      }
    }
    .build {
      position: absolute;
      top: 0;
      left: 0;
      right: 0;
      bottom: 0;
    }
    .build-layout {
      padding: 0 10px;
      height: 100%;
      display: flex;
      align-items: center;
      justify-content: space-between;

      .item {
        //width: 88px;
        width: 23%;
        height: 78%;
        display: flex;
        flex-direction: column;
        justify-content: center;
        margin: 1%;
        background-image: linear-gradient(
          to left,
          rgba(80, 155, 246, 0.1) 2%,
          rgba(80, 155, 246, 0.8) 80%
        );
      }
    }
    .listen {
      color: #fff;
      width: auto;
      background-image: linear-gradient(
        to left,
        rgba(146, 29, 32, 0),
        rgba(146, 29, 32, 1)
      );

      text-align: center;
      position: absolute;
      left: 500px;
      top: 100px;
      font-size: 60px;
    }
    .reg-back-tag {
      position: absolute;
      top: 0;
      left: 0;
      right: 0;
      bottom: 0;

      background: rgba(2, 15, 43, 0.7);
    }
    img {
      top: -33px;
      left: -33px;
      position: absolute;
      width: 66%;
    }
  }
}
