  width: 100%;
  height: 100%;
  font-size: 70px;
}
.showtag {
  position: absolute;
  top: 300px;
  left: 1800px;
  width: 100px;
  height: 1000px;

  > div:first-of-type {
    background: rgba(2, 15, 43, 0.7);
    font-size: 70px;
    color: #fff;
    text-align: center;
    width: 150px;
    margin-bottom: 30px;
    cursor: pointer;
    user-select: none;
    &.active {
      background: rgba(2, 15, 43, 0.911);
      color: aqua;
    }
  }

  .el-checkbox-group {
    width: 100%;
    height: 800px;
    font-size: 50px;
    .el-checkbox-button {
      // width: 100%;
      // height: 25%;
      height: 140px;
    }
  }
}
/deep/ .el-checkbox-button__inner {
  font-size: 50px;
  padding: 0 !important;
  background: rgba(2, 15, 43, 0.7);
  text-align: center;
  line-height: 100px;
  color: #fff;
  border: none;
  display: block;
  width: 150px !important;
  height: 100px !important;
}
/deep/ .el-checkbox-button:first-child .el-checkbox-button__inner {
  border: none;
}
/deep/ .el-checkbox-button.is-checked .el-checkbox-button__inner {
  background: rgba(2, 15, 43, 0.911);
  color: aqua;
}
