.fenxi-enter-active,
.fenxi-leave-active {
  transition: all 0.3s linear;
}
.fenxi-enter,
.fenxi-leave-to {
  opacity: 0;
}
.fenxi-leave,
.fenxi-enter-to {
  opacity: 1;
}
.fenxi {
  position: absolute;
  right: 1710px;
  bottom: 70px;
  font-size: 44px;
  color: #fff;
  z-index: 9;
  cursor: pointer;
  background-color: rgba(2, 15, 43, 0.7);
  padding: 20px 40px;
  border-radius: 20px;
  user-select: none;
  display: flex;
  align-items: center;
  justify-content: center;
}
.fenxi .iconfont {
  font-size: 70px;
}
.fenxi > .active {
  color: #4f9efd;
}
.el-carousel {
  width: 90%;
  margin: 0 auto;
  margin-top: 30px;
}
.el-carousel .economy {
  width: 100%;
  height: 97%;
  display: flex;
  margin: 0 auto;
}
.el-carousel .economy .item {
  width: 50%;
  height: 100%;
  padding-left: 70px;
  display: flex;
  justify-content: space-between;
  justify-items: center;
  align-items: center;
}
.el-carousel .economy .item img {
  width: 150px;
  height: 150px;
}
.el-carousel .economy .item .content {
  text-align: right;
  width: auto;
  height: 180px;
  font-size: 50px;
  margin-right: 70px;
}
.el-carousel .economy .item .content .num {
  padding-bottom: 20px;
}
.el-carousel .economy .item .content span {
  color: #3cbdc3;
  font-size: 70px;
  font-weight: 900;
}
.right {
