  position: absolute;
  right: 27%;
  top: 270px;
  width: 1500px;
  font-size: 50px;
  color: #fff;

  > div:nth-of-type(1) {
    height: 100px;
    background: url("../assets/img/traffic_map_video_bg1.png");
    background-size: cover;
    display: flex;
    justify-content: space-between;
    align-items: center;
    box-sizing: border-box;
    padding: 20px 20px 0 50px;

    > span:last-of-type {
      color: #8f9cff;
      cursor: pointer;
    }
  }
  > div:nth-of-type(2) {
    position: relative;
    // width: 268px;
    // height: 274px;
    width: 99%;
    padding: 50px 0;
    background-color: rgba(2, 15, 43, 0.7);
    margin: 0 auto;

    .more {
      position: absolute;
      bottom: 0;
      width: 100%;
      > img {
        position: absolute;
        bottom: 0;
        width: 100%;
      }
      //   > span {
      //     position: absolute;
      //     bottom: 0;
      //     left: 50%;
      //     transform: translateX(-50%);
      //     font-size: 12px;
      //     color: #00e0ff;
      //   }
    }
  }
  //   > div:nth-of-type(3) {
  //     position: absolute;
  //     width: 100%;
  //     bottom: -100px;
  //     left: 2px;
  //     height: 100px;
  //     background: url("../assets/img/traffic_map_video_bg2.png");
  //     background-size: cover;
  //     z-index: 5;
  //   }
}
.rainpipe-con {
  height: 100%;
  flex-direction: column;
  display: flex;
  justify-content: space-evenly;
  > div {
    display: flex;
    justify-content: space-around;
    opacity: 0.8;
    margin: 10px 0;
    > span:last-of-type {
      width: 68%;
    }
    > span:first-of-type {
      width: 30%;
      text-align: right;
    }
  }
}
.video {
  // padding: ;
  display: flex;
  justify-content: space-evenly;
  > div {
    // margin: 40px auto 0;
    margin-top: 40px;
    margin-bottom: 40px;
    display: block;
    width: 28%;
    height: 300px;
    border: none;
    object-fit: fill;
  }
}
