



































































































.jointleft {
  position: absolute;
  width: 1777px;
  height: 2066px;
  background-color: rgba(2, 15, 43, 0.7);
  top: 264px;
  left: 0px;
}
.toplist {
  height: 291px;
  padding: 60px 50px;
  box-sizing: border-box;
  display: flex;
  justify-content: space-between;

  > div {
    height: 200px;
    width: 524px;
    box-sizing: border-box;
    padding: 20px 0 0 30px;
    > div:first-of-type {
      font-size: 86px;

      > span:last-of-type {
        font-size: 38px;
        margin-left: 20px;
      }
    }
    > div:last-of-type {
      font-size: 38px;
      color: #fff;
    }
  }
  > div:nth-of-type(1) {
    color: #4f9efd;
    background: linear-gradient(
      90deg,
      rgba(79, 158, 253, 0.28),
      rgba(79, 158, 253, 0.04)
    );
  }
  > div:nth-of-type(2) {
    background: linear-gradient(
      90deg,
      rgba(79, 158, 253, 0.28),
      rgba(79, 158, 253, 0.04)
    );
  }
  > div:nth-of-type(3) {
    background: linear-gradient(
      90deg,
      rgba(79, 158, 253, 0.28),
      rgba(79, 158, 253, 0.04)
    );
  }
}
.radio {
  height: 438px;
  padding: 0 50px;
  > div:first-child {
    font-size: 50px;
    width: 690px;
    height: 94px;
    line-height: 88px;
    background: rgba(46, 116, 227, 0.1);
    box-sizing: border-box;
    padding-left: 28px;
  }
  > div:last-child {
    margin-top: 36px;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    > div {
      width: 820px;
      height: 154px;
      box-sizing: border-box;
      padding-right: 30px;
      > div:first-child {
        display: flex;
        justify-content: space-between;
      }
    }
  }
}

/deep/ .el-progress-bar__outer {
  background: rgba(108, 254, 255, 0.3);
  border-radius: 150px;
}
/deep/.el-progress-bar__inner {
  background: linear-gradient(90deg, #388cff, #6cfeff);
}
