.time_weather {
  color: #fff;
  position: absolute;
  right: 20px;
  top: 10px;
  font-size: 38px;
  display: flex;
  cursor: pointer;
}
.time_weather .time {
  text-align: center;
  margin-right: 20px;
}
.time_weather .weather {
  display: flex;
  align-items: center;
  font-size: 50px;
}
.time_weather .weather > div {
  margin-right: 20px;
}
.btn {
  width: 425px;
  height: 127px;
  background: #051b26;
  border: 1px solid #eee;
  margin-top: 78px;
  line-height: 125px;
  color: #fff;
  opacity: 0.7;
  text-align: center;
  font-size: 44px;
  border-radius: 20px;
  cursor: pointer;
}
.btn:hover {
  opacity: 1;
}
.left-flex {
  display: flex;
  justify-content: space-between;
}
.header {
  position: absolute;
  top: 0;
  height: 200px;
  width: 6400px;
}
.header-layout {
  position: absolute;
  top: 0;
  left: 0;
  height: 250px;
  width: 100%;
  background: linear-gradient(to bottom, rgba(2, 15, 43, 0.7) 80%, transparent);
}
.subtitle {
  color: #fff;
  line-height: 40px;
  font-size: 60px;
  letter-spacing: 6px;
  position: absolute;
  width: 1000px;
  margin-left: 50%;
  left: -500px;
  text-align: center;
  top: 180px;
}
.left-layout {
  position: absolute;
  width: 25%;
  left: 12.5%;
  height: 40px;
  z-index: 1;
}
.right-layout {
  position: absolute;
  width: 25%;
  right: 12%;
  height: 40px;
  z-index: 1;
}
.center-content {
  position: absolute;
  width: 1500px;
  height: 180px;
  margin-left: 50%;
  left: -750px;
  font-size: 64px;
  line-height: 170px;
  color: #fff;
  text-align: center;
}
.popover {
  font-size: 42px !important;
  background-color: rgba(2, 15, 43, 0.7) !important;
  color: #fff !important;
  border: none !important;
  top: 213px !important;
}
 /deep/ .el-popper[x-placement^="bottom"] .popper__arrow::after {
  border-bottom-color: rgba(2, 15, 43, 0.7) !important;
}
 /deep/ .el-slider__button-wrapper {
  top: -20px !important;
}
