.fenxi-enter-active,
.fenxi-leave-active {
  transition: all 0.3s linear;
}
.fenxi-enter,
.fenxi-leave-to {
  opacity: 0;
}
.fenxi-leave,
.fenxi-enter-to {
  opacity: 1;
}
.fenxi {
  position: absolute;
  right: 210px;
  bottom: 70px;
  font-size: 44px;
  color: #fff;
  z-index: 9;
  cursor: pointer;
  background-color: rgba(2, 15, 43, 0.7);
  padding: 20px 40px;
  border-radius: 20px;
  user-select: none;
  display: flex;
  align-items: center;
  justify-content: center;
}
.fenxi .iconfont {
  font-size: 70px;
}
.fenxi > .active {
  color: #4f9efd;
}
.footer-con {
  position: absolute;
  z-index: 10;
  height: 330px;
  flex-direction: column;
  display: flex;
  justify-content: space-between;
}
.footer-con > div {
  cursor: pointer;
  background-color: rgba(2, 15, 43, 0.7);
  padding: 20px 40px;
  white-space: nowrap;
  border-radius: 20px;
  user-select: none;
  color: #ccc;
}
.footer-con > div.active {
  color: #fff;
  background-color: rgba(2, 15, 43, 0.9);
}
.footer {
  position: absolute;
  z-index: 10;
  top: -180px;
  left: -25px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  color: #ccc;
  font-size: 48px;
}
