



















































































































































































































/* 特殊方法控制elementui中使用js控制的组件样式，不能使用scoped和deep */
.el-image-viewer__img {
  width: 50%;
}
.container {
  position: absolute;
}
#app {
  font-size: 16px;
}
.back {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
}
.el-carousel__arrow {
  font-size: 40px !important;
}

.router-leave {
  opacity: 1;
}
.router-leave-active {
  transition: all 0.5s;
}
.router-leave-to {
  opacity: 0;
}

.router-enter {
  opacity: 0;
}
.router-enter-active {
  transition: all 0.5s;
}
.router-enter-to {
  opacity: 1;
}
.el-tooltip__popper {
  font-size: 50px !important;
}
