.jointright {
  width: 1248px;
  height: 2066px;
  position: absolute;
  top: 264px;
  right: 0px;
  flex-direction: column;
  display: flex;
  justify-content: space-between;
}
.right_progress {
  background-color: rgba(2, 15, 43, 0.7);
  padding: 0 50px;
  height: 400px;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: center;
}
.right_progress > div {
  width: 500px;
}
.right_progress > div > div:last-of-type {
  margin-top: 20px;
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.right_progress > div > div:last-of-type > div:first-of-type {
  width: 360px;
}
.chart_1 {
  height: 660px;
  background-color: rgba(2, 15, 43, 0.7);
}
.chart_2 {
  height: 560px;
  background-color: rgba(2, 15, 43, 0.7);
}
 /deep/ .el-progress-bar__outer {
  background: rgba(108, 254, 255, 0.3);
  border-radius: 150px;
}
 /deep/ .el-progress-bar__inner {
  background: linear-gradient(90deg, #388cff, #6cfeff);
}
