.control {
  width: 1137px;
  height: 1296px;
  right: 120px;
  top: -30px;
}
.control .control-top {
  display: flex;
  align-items: center;
  justify-content: space-around;
  height: 324px;
}
.control .control-top > div:first-of-type > div:first-of-type {
  font-size: 78px;
}
.control .control-top > div:first-of-type > div:last-of-type {
  opacity: 0.7;
  font-size: 42px;
}
.control .control-top > div:last-of-type > div:first-of-type {
  display: flex;
  align-items: center;
}
.control .control-top > div:last-of-type > div:first-of-type > span {
  opacity: 0.7;
  font-size: 54px;
}
.control .control-top > div:last-of-type > div:last-of-type {
  opacity: 0.7;
  font-size: 42px;
}
.control .control-theme {
  display: flex;
  margin-left: 93px;
  font-size: 42px;
  height: 144px;
  align-items: center;
}
.control .control-theme > div {
  margin-right: 63px;
}
.control .control-theme > div > span {
  margin-right: 30px;
}
.control .control-weather {
  display: flex;
  justify-content: space-evenly;
  padding: 0 60px;
  margin: 30px 0;
}
.control .control-weather > div {
  width: 222px;
  height: 241.5px;
  border-radius: 12px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}
.control .control-weather > div.active {
  background: rgba(87, 87, 122, 0.5);
}
.control .control-time > div:first-of-type {
  margin-left: 93px;
  margin-top: 39px;
}
.control .control-time > div:first-of-type > span {
  margin-right: 30px;
}
.control .control-time > div:nth-of-type(2) {
  display: flex;
  justify-content: space-evenly;
  margin-top: 45px;
  margin-bottom: 63px;
  padding: 0 60px;
}
.control .control-time > div:nth-of-type(2) > div {
  width: 222px;
  height: 214.5px;
  border-radius: 12px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.control .control-time > div:nth-of-type(2) > div.active {
  background: rgba(87, 87, 122, 0.5);
}
.control .control-time > div:last-of-type {
  width: 948px;
  margin: 0 auto;
}
.control .control-time > div:last-of-type > div:first-of-type {
  height: 12px;
  background: rgba(55, 66, 109, 0.64);
  display: flex;
  align-items: center;
}
.control .control-time > div:last-of-type > div:first-of-type > div:first-of-type {
  opacity: 0.9;
  background-image: linear-gradient(90deg, #05a9e6 0%, #09ecff 99%);
  height: 100%;
  width: 300px;
}
.control .control-time > div:last-of-type > div:first-of-type > div:last-of-type {
  width: 120px;
  width: 100%;
  height: 45px;
  background: rgba(0, 69, 129, 0.18);
  font-size: 36px;
  display: flex;
  align-items: center;
  justify-content: center;
  color: #00fff9;
  cursor: pointer;
  user-select: none;
}
.control .control-time > div:last-of-type > div:last-of-type {
  display: flex;
  justify-content: space-between;
  font-size: 36px;
  margin-top: 9px;
}
img {
  width: 78px;
  height: 78px;
  user-select: none;
  -webkit-touch-callout: none;
}
 /deep/ .el-switch.is-checked .el-switch__core {
  background-color: rgba(38, 153, 168, 0.25);
  border-color: #00fff9;
}
 /deep/ .el-switch.is-checked .el-switch__core:after {
  background-color: #00fff9;
  left: 80%;
}
 /deep/ .el-switch__core {
  background-color: rgba(22, 87, 117, 0.16);
  border-color: #2c448b;
  height: 60px;
  border-radius: 30px;
  width: 120px !important;
}
 /deep/ .el-switch__core:after {
  background-color: #2c448b;
  width: 30px;
  height: 30px;
  top: 10.5px;
}
 /deep/ .el-divider--horizontal {
  margin: 0 auto;
  width: 84%;
}
 /deep/ .el-divider {
  opacity: 0.6;
  background-image: linear-gradient(to right, #cbd4ff, #fff 50%, #cbd4ff);
  border-radius: 8px;
}
 /deep/ .el-slider__runway {
  height: 18px;
}
 /deep/ .el-slider__button {
  width: 32px;
  height: 32px;
}
 /deep/ .el-slider__bar {
  height: 18px;
}
 /deep/ .el-slider__button-wrapper {
  width: 32px !important;
  height: 32px !important;
  top: -19px !important;
}
