.iconfont {
  font-size: 70px;
}
.footer {
  position: absolute;
  z-index: 10;
  top: -580px;
  left: 0;
  width: 2000px;
  height: 150px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  color: #ccc;
  font-size: 70px;
}
.dynamicwater {
  position: absolute;
  left: -10%;
  bottom: -380px;
  width: 100px;
  height: 1800px;
  display: flex;
  align-items: center;
  justify-content: center;
}
.footer-con {
  position: absolute;
  z-index: 10;
  height: 990px;
  flex-direction: column;
  display: flex;
  justify-content: space-between;
}
.footer-con > div {
  cursor: pointer;
  background-color: rgba(2, 15, 43, 0.7);
  padding: 20px 40px;
  border-radius: 20px;
  user-select: none;
}
.footer-con > div.active {
  color: #fff;
  background-color: rgba(2, 15, 43, 0.9);
}
