  font-size: 70px;
}
.footer {
  position: absolute;
  z-index: 10;
  top: -580px;
  left: 0;
  // transform: translateX(-50%);
  width: 2000px;
  height: 150px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  color: #ccc;
  font-size: 70px;
}
.dynamicwater {
  position: absolute;
  left: -10%;
  // top: -1900px;
  bottom: -380px;
  width: 100px;
  height: 1800px;
  display: flex;
  align-items: center;
  justify-content: center;
}

.footer-con {
  position: absolute;
  z-index: 10;
  // top: -1000px;
  height: 990px;
  flex-direction: column;
  display: flex;
  justify-content: space-between;
  > div {
    cursor: pointer;
    background-color: rgba(2, 15, 43, 0.7);
    padding: 20px 40px;
    border-radius: 20px;
    user-select: none;
  }
  > div.active {
    color: #fff;
    background-color: rgba(2, 15, 43, 0.9);
  }
}
/deep/.el-slider.is-vertical .el-slider__runway {
  width: 30px;
}
/deep/.el-slider__button {
  width: 60px;
  height: 60px;
}
/deep/.el-tooltip__popper {
  font-size: 50px !important;
}
.tooltip {
  font-size: 50px !important;
}
/deep/.el-slider__button-wrapper {
  width: 60px !important;
  height: 60px !important;
}
/deep/.el-slider.is-vertical .el-slider__button-wrapper {
  left: -19px !important;
}
/deep/.el-slider.is-vertical .el-slider__bar {
  width: 30px;
}
/deep/.el-slider__marks-text {
  font-size: 40px;
  color: aqua;
}
/deep/.el-slider.is-vertical .el-slider__marks-text {
  left: 70px;
}
/deep/.el-slider__stop {
  background-color: transparent;
}
