.rainpipe {
  position: absolute;
  right: 27%;
  top: 270px;
  width: 1500px;
  font-size: 50px;
  color: #fff;
}
.rainpipe > div:nth-of-type(1) {
  height: 100px;
  background: url("../assets/img/traffic_map_video_bg1.png");
  background-size: cover;
  display: flex;
  justify-content: space-between;
  align-items: center;
  box-sizing: border-box;
  padding: 20px 20px 0 50px;
}
.rainpipe > div:nth-of-type(1) > span:last-of-type {
  color: #8f9cff;
  cursor: pointer;
}
.rainpipe > div:nth-of-type(2) {
  position: relative;
  width: 99%;
  padding: 50px 0;
  background-color: rgba(2, 15, 43, 0.7);
  margin: 0 auto;
}
.rainpipe > div:nth-of-type(2) .more {
  position: absolute;
  bottom: 0;
  width: 100%;
}
.rainpipe > div:nth-of-type(2) .more > img {
  position: absolute;
  bottom: 0;
  width: 100%;
}
.rainpipe-con {
  height: 100%;
  flex-direction: column;
  display: flex;
  justify-content: space-evenly;
}
.rainpipe-con > div {
  display: flex;
  justify-content: space-around;
  opacity: 0.8;
  margin: 10px 0;
}
.rainpipe-con > div > span:last-of-type {
  width: 68%;
}
.rainpipe-con > div > span:first-of-type {
  width: 30%;
  text-align: right;
}
.video {
  display: flex;
  justify-content: space-evenly;
}
.video > div {
  margin-top: 40px;
  margin-bottom: 40px;
  display: block;
  width: 28%;
  height: 300px;
  border: none;
  object-fit: fill;
}
